'use client';

import { useState, useEffect, useRef, useCallback }from 'react';
import { Switch } from '@/components/ui/switch';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Progress } from './ui/progress';
import { Button } from './ui/button';
import { Input } from './ui/input';
import { Textarea } from './ui/textarea';
import { Tabs, TabsContent, TabsList, TabsTrigger } from './ui/tabs';


const timeOptions = [
  { value: 5, label: 'Every 5 seconds' },
  { value: 10, label: 'Every 10 seconds' },
  { value: 20, label: 'Every 20 seconds' },
  { value: 30, label: 'Every 30 seconds' },
  { value: 60, label: 'Every minute' },
  { value: 300, label: 'Every 5 minutes' },
  { value: 600, label: 'Every 10 minutes' },
  { value: 900, label: 'Every 15 minutes' },
  { value: 1800, label: 'Every 30 minutes' },
  { value: 3600, label: 'Every hour' },
  { value: 7200, label: 'Every 2 hours' },
  { value: 86400, 'label': 'Every day' },
];

export function ScheduledDistractions() {
  const [isEnabled, setIsEnabled] = useState(false);
  const [permission, setPermission] = useState('default');
  const [intervalSeconds, setIntervalSeconds] = useState(300); // Default to 5 minutes

  // For one-time alarm
  const [reminderTime, setReminderTime] = useState('');
  const [reminderMessage, setReminderMessage] = useState('');

  const { toast } = useToast();
  
  const [timeLeft, setTimeLeft] = useState(0);
  const [progress, setProgress] = useState(100);
  const countdownRef = useRef<number | null>(null);
  const intervalRef = useRef<NodeJS.Timeout | null>(null);


  useEffect(() => {
    // Check for notification permission on mount
    if (typeof window !== 'undefined' && 'Notification' in window) {
      setPermission(Notification.permission);
    }
    
    // Register service worker
    if ('serviceWorker' in navigator && !navigator.serviceWorker.controller) {
      navigator.serviceWorker.register('/sw.js').catch(err => {
        console.error('Service worker registration failed:', err)
      })
    }
  }, []);

  const startCountdown = useCallback(() => {
    if (intervalRef.current) clearInterval(intervalRef.current);
    if (countdownRef.current) cancelAnimationFrame(countdownRef.current);

    setTimeLeft(intervalSeconds);
    setProgress(100);

    const animate = (startTime: number) => {
        const elapsedTime = (Date.now() - startTime) / 1000;
        const newTimeLeft = Math.max(0, intervalSeconds - elapsedTime);
        setTimeLeft(newTimeLeft);
        setProgress((newTimeLeft / intervalSeconds) * 100);

        if (newTimeLeft > 0) {
            countdownRef.current = requestAnimationFrame(() => animate(startTime));
        }
    };
    
    const startCycle = () => {
        const startTime = Date.now();
        animate(startTime);
    }
    
    startCycle(); // initial run
    intervalRef.current = setInterval(startCycle, intervalSeconds * 1000);

  }, [intervalSeconds]);

  const stopCountdown = () => {
    if (intervalRef.current) clearInterval(intervalRef.current);
    if (countdownRef.current) cancelAnimationFrame(countdownRef.current);
    setProgress(100);
    setTimeLeft(0);
  }

  const sendMessageToSw = useCallback(async (message: any) => {
      if ('serviceWorker' in navigator && navigator.serviceWorker.controller) {
          navigator.serviceWorker.controller.postMessage(message);
      } else if ('serviceWorker' in navigator) {
          const registration = await navigator.serviceWorker.ready;
          registration.active?.postMessage(message);
      }
  }, []);


  useEffect(() => {
    if (isEnabled && permission === 'granted') {
      sendMessageToSw({ type: 'start-recurring', payload: { intervalSeconds }});
      startCountdown();
    } else {
      sendMessageToSw({ type: 'stop-recurring' });
      stopCountdown();
    }
    
    return () => {
        stopCountdown();
    }
  }, [isEnabled, permission, intervalSeconds, sendMessageToSw, startCountdown]);


  const handleToggle = async (checked: boolean) => {
    if (checked) {
      if (typeof window === 'undefined' || !('Notification' in window)) {
        toast({
          variant: 'destructive',
          title: 'Not Supported',
          description: 'Your browser does not support notifications.',
        });
        return;
      }
      
      let currentPermission = permission;
      if (currentPermission !== 'granted') {
        currentPermission = await Notification.requestPermission();
        setPermission(currentPermission);
      }

      if (currentPermission === 'granted') {
        setIsEnabled(true);
        toast({
            title: 'Distractions Enabled!',
            description: "Get ready to be wonderfully unproductive, even in the background.",
        });
      } else {
        toast({
          variant: 'destructive',
          title: 'Permission Denied',
          description: 'You have blocked notifications. Please enable them in your browser settings.',
        });
        return;
      }
    } else {
      setIsEnabled(false);
      toast({
            title: 'Distractions Disabled',
            description: "Okay, back to work... for now.",
      });
    }
  };

  
  const formatTime = (totalSeconds: number) => {
    const hours = Math.floor(totalSeconds / 3600);
    const minutes = Math.floor((totalSeconds % 3600) / 60);
    const secs = Math.floor(totalSeconds % 60);
    if (hours > 0) {
      return `${String(hours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}:${String(secs).padStart(2, '0')}`;
    }
    return `${String(minutes).padStart(2, '0')}:${String(secs).padStart(2, '0')}`;
  };

  const handleSetReminder = () => {
    if (permission !== 'granted') {
         toast({
            variant: 'destructive',
            title: 'Permission Required',
            description: 'Please enable notifications first to set a reminder.',
        });
        handleToggle(true); // Trigger permission request
        return;
    }

    if (!reminderTime) {
      toast({
        variant: 'destructive',
        title: 'No Time Set',
        description: 'Please pick a time for your reminder.',
      });
      return;
    }

    const [hours, minutes] = reminderTime.split(':').map(Number);
    const now = new Date();
    const reminderDate = new Date();
    reminderDate.setHours(hours, minutes, 0, 0);

    if (reminderDate <= now) {
      reminderDate.setDate(reminderDate.getDate() + 1); // Schedule for tomorrow if time has passed
    }

    const timestamp = reminderDate.getTime();
    
    sendMessageToSw({
        type: 'show-one-time',
        payload: {
            timestamp,
            message: reminderMessage || 'This is your scheduled distraction!',
        },
    });

    toast({
      title: 'Reminder Set!',
      description: `We'll distract you at ${reminderDate.toLocaleTimeString()}.`,
    });
  };

  const getFrequencyLabel = () => {
    return timeOptions.find(o => o.value === intervalSeconds)?.label.replace('Every ', '');
  }

  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between rounded-lg bg-muted/50 p-4">
         <Label htmlFor="distraction-mode" className="font-semibold flex-1">
            Enable Background Distractions
          </Label>
        <Switch
          id="distraction-mode"
          checked={isEnabled}
          onCheckedChange={handleToggle}
          aria-label="Enable or disable scheduled distractions"
        />
      </div>

       {isEnabled ? (
         <p className="text-sm text-muted-foreground px-1">
            You'll get a tempting notification every {getFrequencyLabel()}. Good luck focusing!
        </p>
       ) : (
        <p className="text-sm text-muted-foreground px-1">
          Ready to embrace procrastination? Flip the switch! Or set a one-time reminder below.
        </p>
       )}
      
      <Tabs defaultValue="recurring" className="w-full">
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger value="recurring">Recurring</TabsTrigger>
          <TabsTrigger value="onetime">One-Time</TabsTrigger>
        </TabsList>
        <TabsContent value="recurring" className="space-y-4 pt-4">
           <div>
              <Label htmlFor="distraction-interval">Distraction Frequency</Label>
              <Select
                  value={String(intervalSeconds)}
                  onValueChange={(value) => setIntervalSeconds(Number(value))}
              >
                  <SelectTrigger id="distraction-interval">
                      <SelectValue placeholder="Select interval" />
                  </SelectTrigger>
                  <SelectContent>
                      {timeOptions.map(option => (
                          <SelectItem key={option.value} value={String(option.value)}>
                              {option.label}
                          </SelectItem>
                      ))}
                  </SelectContent>
              </Select>
            </div>
            {isEnabled && (
              <div>
                <Label>Next distraction in</Label>
                <div className="flex items-center gap-4 mt-1">
                    <Progress value={progress} className="h-3 flex-1"/>
                    <span className="font-mono text-lg font-semibold">{formatTime(timeLeft)}</span>
                </div>
              </div>
            )}
        </TabsContent>
        <TabsContent value="onetime" className="space-y-4 pt-4">
          <div>
            <Label htmlFor="reminder-time">Reminder Time (your local time)</Label>
            <Input 
              id="reminder-time" 
              type="time"
              value={reminderTime}
              onChange={(e) => setReminderTime(e.target.value)}
            />
          </div>
           <div>
            <Label htmlFor="reminder-message">Reminder Message (Optional)</Label>
            <Textarea 
              id="reminder-message" 
              placeholder="e.g., 'Stop working and watch a movie'"
              value={reminderMessage}
              onChange={(e) => setReminderMessage(e.target.value)}
              rows={2}
            />
          </div>
          <Button onClick={handleSetReminder} className="w-full">Set Reminder</Button>
        </TabsContent>
      </Tabs>
    </div>
  );
}
